.custombtn {
  color: black;
  background-color: transparent;
  border: none;
  border-radius: 5px;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.custombtn:hover {
  color: #fff;
  background-color: #007bff;
  border:none;
}
.custombtn:not(:disabled):not(.disabled):active, .custombtn:not(:disabled):not(.disabled).active,
.show > .custombtn.dropdown-toggle {
  color: #fff;
  background-color: #007bff;
  border-color:none;
  border: none;
}
.custombtn:focus{
  outline: none;
}
